@import 'color';

#summaryPanel {
    border-radius: 4px;
    border: 1px solid $brand-gray;
    box-shadow: 0 0 1em 0 rgba(0, 0, 0, 0.1);
    p {
         margin: 0em;
    }
}

#fileBrowser {
    width: 388px;
    height: 76px;
    margin-left: -194px;
    margin-top: -38px;
}

.draggable-dialog {
    display: none;

    .content {
        padding: 0 5px;
        background: $white;
        cursor: default;
        table {
            border: 1px solid $brand-gray;
        }
    }

    table {
        overflow: auto;
        width: 100%;
    }

    table,
    td,
    th {
        border-collapse: collapse;
    }

    td,
    th {
        text-align: center !important;
        padding: 3px !important;
        height: 2em;
        font-size: 14px;
    }

    #chart {
        width: 100%;
        height: 20em;
        border: 1px solid $brand-gray;
        border-radius: 3px;
    }
}

.dialog-content {
    padding: 0 15px 15px 15px;
    background: white;
    cursor: default;
}

.warn .react-grid-Cell {
    background: #fbeed5;
}
.success .react-grid-Cell {
    background: #dff0d8;
}
.error .react-grid-Cell {
    background: #f2dede;
}
.info .react-grid-Cell {
    background: #d9edf7;
}

// ReactGrid
.ui-widget {
    .react-grid-Main {
        outline: none;
    }
    .react-grid-Grid {
        border: 1px solid #f2f2f2;
    }
    .react-grid-Cell {
        border-bottom: 1px solid #f2f2f2;
        border-right: 1px solid #f2f2f2;
    }
    .react-grid-HeaderCell {
        font-size: 14px;
        border-bottom: 1px solid #f2f2f2;
        border-right: 1px solid #f2f2f2;
    }
    .react-grid-Row {
        font-size: 14px;
    }
}

#tradeInfo {
    margin-bottom: 10px;
}

#chart-dialog-component,
#trading-view-dialog-component {
    width: 100% !important;
}

.icon-close {
    width: 1.5em !important;
    height: 1.5em !important;
}

.ui-dialog-content {
    padding: 0px !important;
}
